import React, { useState, useContext } from 'react'
import { GlobalContext } from '../contexts/Globalstate'
import { v4 as uuidv4 } from 'uuid';

export default function Addtranscation() {
    const [text, setText] = useState('')
    const [amount, setAmount] = useState(0)
    const { addTransaction } = useContext(GlobalContext)
    function onSubmit(e) {
        e.preventDefault();

        const newtrans = {
            id: uuidv4(),
            text, amount: +amount
        }
        if (amount !== 0 && text !== '') {
            addTransaction(newtrans);
        }

    }
    return (
        <form className="add-container" onSubmit={onSubmit}>
            <h5>Add New Transcation</h5>
            <input type="text" placeholder="Enter Amount" value={amount} onChange={(e) => { setAmount(e.target.value) }} />
            <input type="text" placeholder="Add Transation" value={text} onChange={(e) => { setText(e.target.value) }} />
            <button className="btn-add">Add Transcation</button>
        </form>
    )
}
