import React, { useContext } from 'react'
import { GlobalContext } from '../contexts/Globalstate'

export default function Histroy() {
    const red = { borderRight: "5px solid red" };
    const green = { borderRight: "5px solid #61ee0d" };
    const { transactions } = useContext(GlobalContext)
    const { deleteTransaction } = useContext(GlobalContext)
    return (
        <div >
            <h5>Histroy</h5>
            <div className="history-container">
                <ul>
                    {transactions.map(trans => (<li style={trans.amount > 0 ? green : red} key={trans.id}><i className="fa fa-window-close times" aria-hidden="true" onDoubleClick={() => (deleteTransaction(trans.id))} />{trans.text}<span>{trans.amount} $</span></li>))}
                </ul>
            </div>
        </div>
    )
}
