import React, { useContext } from 'react'
import { GlobalContext } from '../contexts/Globalstate'

export default function IncomeExpense() {
    const { transactions } = useContext(GlobalContext)
    const amount = transactions.map(trans => (trans.amount));
    const iamount = amount.filter((acc) => acc > 0)
    const eamount = amount.filter((acc) => acc < 0)
    const income = iamount.reduce((acc, item) => (acc += item), 0).toFixed(2);
    const expense = eamount.reduce((acc, item) => (acc += item), 0).toFixed(2);
    return (
        <div className="status-container">
            <div className="income">
                <div>INCOME</div>
                <div>${income}</div>
            </div>
            <div className="expense">
                <div>EXPENSE</div>
                <div>${Math.abs(expense)}</div>
            </div>
        </div>
    )
}
